import React from "react";
import {
  Card,
  Input,
  InputNumber,
  Tooltip,
  Select,
  Switch,
  Form,
  Layout,
  Space,
  Button,
  PageHeader,
  Tabs,
} from "antd";
import { Environment, Feature, Settings } from "../../types";
import ThemeToggle from "../ui/theme/themeToggle";
import { useQuery } from "react-query3";
import queryClient, { useMutation } from "components/utils/queryClient";
import useAppStore from "components/context/app/Hooks";
import { FileZipOutlined, HistoryOutlined } from "@ant-design/icons";

export default function SettingsPage() {
  const [form] = Form.useForm();
  const { licensed, userData } = useAppStore();
  const { data: settings, isLoading } = useQuery<Settings>("/settings");
  const { data: environments } = useQuery<Environment[]>("/environment");
  const { mutate: update, isLoading: isUpdateLoading } = useMutation({
    onSuccess: () => queryClient.invalidateQueries("/settings"),
  });

  const isLicensed = licensed(Feature.Automation);

  function onFinish(values) {
    update({
      key: "/settings",
      action: "update",
      ...{
        ...settings,
        ...values,
      },
    });
  }

  return (
    <PageHeader title="General" subTitle="General configuration settings.">
      <Layout>
        <Layout.Content style={{ padding: 24 }}>
          <Space size="large" direction="vertical" style={{ width: "100%" }}>
            <Card bordered={false} loading={isLoading}>
              <Form
                form={form}
                onFinish={onFinish}
                name="main_settings_form"
                labelCol={{ span: 8 }}
                wrapperCol={{ span: 16 }}
                labelAlign="left"
                initialValues={{
                  ...settings,
                  repositoryPath: settings?.repositoryPath,
                  databasePath: settings?.databasePath,
                  apiEnvironment: settings?.apiEnvironment || "Integrated",
                  defaultEnvironment: settings?.defaultEnvironment || "Integrated",
                  securityEnvironment: settings?.securityEnvironment || "Integrated",
                }}
              >
                <Tabs tabPosition="left">
                  <Tabs.TabPane tab="Admin Console" key="console">
                    <Form.Item name="adminConsoleTitle" label="Admin Console Title" tooltip="The title to display in the Admin Console title bar.">
                      <Input />
                    </Form.Item>
                    <Form.Item name="adminConsoleLogo" label="Admin Console Logo" tooltip="The logo to display in the Admin Console title bar.">
                      <Input />
                    </Form.Item>
                    <Form.Item
                      name="defaultPage"
                      label="Default Page"
                      tooltip="The page to navigate after you login."
                    >

                      <Select defaultValue={settings?.defaultPage}>
                        <Select.Option value="home">
                          Home
                        </Select.Option>
                        <Select.Option value="apis/endpoints">
                          Endpoints
                        </Select.Option>
                        <Select.Option value="apis/ratelimits">
                          Rate Limits
                        </Select.Option>
                        <Select.Option value="automation/scripts">
                          Scripts
                        </Select.Option>
                        <Select.Option value="automation/jobs">Jobs</Select.Option>
                        <Select.Option value="automation/schedules">
                          Schedules
                        </Select.Option>
                        <Select.Option value="automation/triggers">
                          Triggers
                        </Select.Option>
                        <Select.Option value="dashboards">Dashboards</Select.Option>
                        <Select.Option value="dashboards/frameworks">
                          Frameworks
                        </Select.Option>
                        <Select.Option value="dashboards/modules">
                          Components
                        </Select.Option>
                        <Select.Option value="dashboards/folders">
                          Published Folders
                        </Select.Option>
                        <Select.Option value="dashboards/marketplace">
                          Marketplace
                        </Select.Option>
                        <Select.Option value="platform/variables">
                          Variables
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="settings"
                        >
                          Settings
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="settings/tags"
                        >
                          Tags
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="settings/environments"
                        >
                          Environments
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="settings/configurations"
                        >
                          Configurations
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="settings/license"
                        >
                          License
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="security/roles"
                        >
                          Roles
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="security/identities"
                        >
                          Identities
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="security/tokens"
                        >
                          Tokens
                        </Select.Option>
                        <Select.Option
                          disabled={!userData?.roles.includes("Administrator")}
                          value="security/authentications"
                        >
                          Authentications
                        </Select.Option>
                      </Select>
                    </Form.Item>

                    {/* UI settings */}
                    <Form.Item name="hideApi" label="Hide API Features">
                      <Switch
                        defaultChecked={settings?.hideApi}
                        checkedChildren="Yes"
                        unCheckedChildren="No"
                      />
                    </Form.Item>
                    <Form.Item name="hideAutomation" label="Hide Automation Features">
                      <Switch
                        defaultChecked={settings?.hideAutomation}
                        checkedChildren="Yes"
                        unCheckedChildren="No"
                      />
                    </Form.Item>
                    <Form.Item name="hideDashboard" label="Hide User Interface Features">
                      <Switch
                        defaultChecked={settings?.hideDashboard}
                        checkedChildren="Yes"
                        unCheckedChildren="No"
                      />
                    </Form.Item>
                    <Form.Item name="hideHomePage" label="Hide Home Page">
                      <Switch
                        defaultChecked={settings?.hideHomePage}
                        checkedChildren="Yes"
                        unCheckedChildren="No"
                      />
                    </Form.Item>
                    <Form.Item label="Dark Theme">
                      <ThemeToggle />
                    </Form.Item>
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Automation" key="automation">
                    <Form.Item name="concurrentJobLimit" label="Concurrent Job Limit" tooltip="The number of jobs that can run at once.">
                      {isLicensed ? (
                        <InputNumber
                          defaultValue={settings?.concurrentJobLimit}
                          min={2}
                          max={1000}
                        />
                      ) : (
                        <Tooltip
                          color="blue"
                          title="Concurrent Jobs can only be increased when licensed."
                        >
                          <InputNumber min={2} max={2} defaultValue={2} />
                        </Tooltip>
                      )}
                    </Form.Item>
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Data" key="data">
                    <Form.Item name="repositoryPath" label="Repository Path">
                      <Input disabled />
                    </Form.Item>
                    <Form.Item name="databasePath" label="Database Path">
                      <Input disabled />
                    </Form.Item>
                    <Form.Item name="scriptBaseFolder" label="Script Base Folder" tooltip="The base path to store scripts within the repository.">
                      <Input />
                    </Form.Item>
                    <Form.Item name="groomDays" label="Data retention" tooltip="The number of days to retain data from jobs. The default is 30 days.">
                      <InputNumber min={0} max={60} />
                    </Form.Item>
                    <Form.Item name="groomInterval" label="Data groom interval" tooltip="How frequently, in minutes, the data groom job is run.">
                      <InputNumber min={1} max={59} />
                    </Form.Item>
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Diagnostics" key="diagnostics">
                    <Form.Item name="logLevel" label="Log Level" tooltip="Logging level for PowerShell Universal">
                      <Select defaultValue={settings?.logLevel}>
                        <Select.Option value="Debug">Debug</Select.Option>
                        <Select.Option value="Information">
                          Information
                        </Select.Option>
                        <Select.Option value="Warning">Warning</Select.Option>
                        <Select.Option value="Error">Error</Select.Option>
                      </Select>
                    </Form.Item>
                    <Form.Item name="microsoftLogLevel" label="Microsoft Log Level" tooltip="Logging level for the Microsoft components, like the webserver, of PowerShell Universal.">
                      <Select defaultValue={settings?.microsoftLogLevel}>
                        <Select.Option value="Debug">Debug</Select.Option>
                        <Select.Option value="Information">
                          Information
                        </Select.Option>
                        <Select.Option value="Warning">Warning</Select.Option>
                        <Select.Option value="Error">Error</Select.Option>
                      </Select>
                    </Form.Item>
                    <Space>
                      <Button href="/api/v1/log" icon={<FileZipOutlined />}>Download Logs</Button>
                      <Button href="/hangfire" icon={<HistoryOutlined />}>Job Details</Button>
                    </Space>
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Environments" key="environments">
                    <Form.Item
                      name="apiEnvironment"
                      label="API Environment"
                      tooltip="The default environment to use when invoking API endpoints. This overrides the Default Environment."
                    >
                      <Select>
                        {environments?.map((env) => (
                          <Select.Option key={env.name} value={env.name}>
                            {env.name}
                          </Select.Option>
                        ))}
                      </Select>
                    </Form.Item>
                    <Form.Item name="defaultEnvironment" label="Default Environment" tooltip="The default environment to use for all features.">
                      <Select>
                        {environments?.map((env) => (
                          <Select.Option key={env.name} value={env.name}>
                            {env.name}
                          </Select.Option>
                        ))}
                      </Select>
                    </Form.Item>
                    <Form.Item
                      name="securityEnvironment"
                      label="Security Environment"
                      tooltip="By default, authentication and authorization happen within the Universal.Server.exe process. To run these out of process, select an environment to run on"
                    >
                      <Select>
                        {environments?.map((env) => (
                          <Select.Option key={env.name} value={env.name}>
                            {env.name}
                          </Select.Option>
                        ))}
                      </Select>
                    </Form.Item>
                  </Tabs.TabPane>
                  <Tabs.TabPane tab="Platform" key="platform">
                    <Form.Item
                      name="disableAutoReload"
                      label="Disable Auto Reload"
                      tooltip="Server-wide setting that will disable the auto reload configuration files if they change on disk"
                    >
                      <Switch
                        defaultChecked={settings?.disableAutoReload}
                        checkedChildren="Yes"
                        unCheckedChildren="No"
                      />
                    </Form.Item>
                    <Form.Item name="disableUpdateCheck" label="Disable Update Check" tooltip="Prevents PowerShell Universal from checking for updates.">
                      <Switch
                        defaultChecked={settings?.disableUpdateCheck}
                        checkedChildren="Yes"
                        unCheckedChildren="No"
                      />
                    </Form.Item>
                  </Tabs.TabPane>
                </Tabs>





                <Form.Item>
                  <Button
                    loading={isUpdateLoading}
                    type="primary"
                    htmlType="submit"
                  >
                    Save Changes
                  </Button>
                </Form.Item>
              </Form>
            </Card>
          </Space>
        </Layout.Content>
      </Layout>
    </PageHeader>
  );
}
